.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App h1 {
  color: #ffff;
  background-color: #CCA353;
  margin: 0;
  padding: 1rem;
  font-size: 48px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Business {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 16.66rem;
    margin: 0 .5rem 2.3rem .5rem;
  }
  
  .Business .image-container {
    height: 16.66rem;
    margin-bottom: .5rem;
  }

  .Business .image-container img {
    height: 100%;
    width: 100%;
  }

  .Business .image-container img:hover {
    height: 105%;
    width: 105%;
    transition: 0.3s;
  }
  
  .Business h2 {
    margin-bottom: .5rem;
    font-size: 1.5rem;
    font-weight: 600;
    text-align: left;
  }
  
  .Business-information {
    display: flex;
    justify-content:space-between;
    line-height: .3rem;
  }
  
  .Business-information p {
    font-size: 1rem;
    font-weight: 300;
  }
  
  .Business-address {
    text-align: left;
  }

  .Business-address a {
    text-decoration: none;
    color: black;
  }

  .Business-address a:hover {
    text-decoration: underline;
    color:saddlebrown;
  }
  
  .Business-reviews {
    text-align: right;
  }

  .Business-reviews #rat {
    color: #CCA353;
  }
  
  .Business-reviews h3 {
    text-transform: uppercase;
    color: #CCA353;
    font-weight: 600;
    margin-bottom: .1rem;
    margin-top: 1.2rem;
  }
  
  .Business-reviews .rating {
    font-size: .88rem;
    line-height: 1rem;
  }
.BusinessList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 4.4rem 10%;
  }
.SearchBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url(/ravenous/static/media/background_search_desktop.902df4c5.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 25rem;
}

.SearchBar-sort-options ul {
    display: flex;
    justify-content: center;
    color: white;
    margin-bottom: 2.22px;
}

.SearchBar-sort-options li {
    cursor: pointer;
    width: 4rem;
    border-bottom: 1px solid white;
    padding: 0 3rem 1rem 3rem;
    line-height: 1.5;
    text-align: center;
    font-weight: 600;
    font-size: 1rem;
    transition: color .25s;
    list-style-type: none;
}

.SearchBar-fields input {
  margin: 40px 20px;
  width: 30%;
  line-height: 2rem;
  border-radius: .5rem;
  text-indent: 15px;
}

.SearchBar-fields input:focus {
  outline: none;
}

.SearchBar-submit button {
  cursor: pointer;
  padding: 15px 35px;
  background-color: #CCA353;
  border: none;
  border-radius: .5rem;
  color: #ffff;
  font-weight: 900;
  font-size: large;
}

.SearchBar-submit button:hover {
  background-color: #B8860B;
}


.SearchBar-sort-options li:hover {
    color: #d4cfcf;
}

.SearchBar-sort-options li.active, 
.SearchBar-sort-options li.active.hover {
    border-bottom: 1px solid #f0c36c;
    color: #f0c36c;
}

@media only screen and (max-width: 560px) {
    .SearchBar {
      background-image: url(/ravenous/static/media/background_search_mobile.5d0905e6.jpg);
    }
  
    .SearchBar-sort-options ul {
      margin-left: 2rem;
      margin-right: 2rem;
    }
  
    .SearchBar-fields {
      flex-direction: column;
      align-items: center;
    }
  
    .SearchBar-fields input {
      margin-right: 0;
      margin-bottom: .86rem;
    }
  }
